.chatbox {
      display: flex;
      flex-direction: column;
      width: 70%;
      font-family: cursive;
}

.chatbox_header {
      display: flex;
      flex-direction: row;
      border: 1px solid lightgray;
      height: 10%;
      padding: 10px;
      align-items: center;
}

.chatbox_header_details {
      display: flex;
      flex-direction: column;
      margin-left: 10px;
      padding: 0em;
}

.chatbox_header_avatar {
      display: grid;
      place-items: center;
      padding-left: 1rem;
}

.exit_icon {
      margin-left: auto;
      display: grid;
      place-items: center;
}

.message_scroll {
      overflow-y: scroll;
      padding: 10px;
      height: 80%;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.message_scroll::-webkit-scrollbar {
      display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.message_scroll {
      -ms-overflow-style: none; /* IE and Edge */
      scrollbar-width: none; /* Firefox */
}

.chatbox_message_sender {
      padding: 5px;
      position: relative;
      background-color: white;
      border-radius: 10px;
      width: fit-content;
      margin: 10px;
      width: fit-content;
      font-size: 0.8rem;
}

.chatbox_message_receiver {
      margin-left: auto;
      background-color: #dcf8c6;
}

.span_name {
      position: relative;
      width: fit-content;
      padding: 0;
}

/* .span_time {
      position: relative;
      margin-left: 10px;
      font-size: xx-small;
      bottom: auto;
} */

.chatbox_footer {
      height: 10%;
      background-color: lightgray;
}

.chatbox_footer_form {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      padding: 10px;
}

.chatbox_footer_form > input {
      margin-left: 10px;
      flex: 1;
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      border-radius: 10px;
}
.form_button {
      display: none;
}
@media only screen and (max-width: 600px) {
      .chatbox {
            width: 100%;
            height: 80%;
      }

      .chatbox_header_details {
            display: flex;
            flex-direction: column;
            margin-left: 5px;
            padding: 0em;
            font-size: 1rem;
      }

      .chatbox_header_avatar {
            display: grid;
            place-items: center;
            padding-left: 0rem;
      }
}
